@import 'assets/css/_mixins.scss';

.plate {
    margin: auto auto 25px auto;

    .container-image {
        border: 3px solid #FFFFFF;
        border-bottom: 0px;
        border-radius: 0px 20px 0px 0px;
        padding: 5px 0px 5px 0px;
        margin: auto;
        text-align: center;
        box-shadow: 0 3px 15px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);

        .image {
            width: 100%;
            width: 160px;
        }
    }

    .content {
        text-align: center;
        position: relative;
        background-color: #FFFFFF;
        border-radius: 0px 0px 20px 20px;
        padding: 15px 8px 15px 8px;
        box-shadow: 0 3px 15px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);

        .title {
            font-size: 22px;
            color: $main-color-1;
            margin-bottom: 5px;
        }
        .description {
            margin-bottom: 15px;
        }
        .bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .price {
                font-size: 18px;
                color: $main-color-2;
                text-align: right;
            }
            .btn-order {
                color: #FFFFFF;
                text-decoration: none;
                border: 1px solid $main-color-2;
                background-color: $main-color-2;
                padding: 5px 8px 5px 8px;
                border-radius: 0px 0px 0px 20px;
            }
        }
    }

    @media (min-width: 1024px) {
        position: relative;
        display: flex;
        justify-content: flex-end;
        min-width: 314px;

        &:hover {
            .content {
                background-color: $main-color-1;
                @include transform (translate(0px, -10px));

                .title, .description { color: #FFFFFF }
                .bottom { 
                    .price { opacity: 0; z-index: -1 }
                    .btn-order { display: block }
                }
            }
        }

        .container-image {
            border: 0px solid #FFFFFF;
            padding: 0px 0px 0px 0px;
            box-shadow: 0px 0px;
            position: absolute;
            left: 0;
            bottom: 0;
            margin-bottom: -5px;
            z-index: 1;
    
            .image {
                width: 100%;
                width: 160px;
            }
        }

        .content {
            position: relative;
            background-color: #FFFFFF;
            border-radius: 0px 0px 25px 20px;
            padding: 15px 15px 15px 15px;
            width: 70%;
            transition: background-color 0.3s, transform 0.3s;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-end;
            min-height: 185px;

            .title {
                font-family: $Freehand-Regular;
                letter-spacing: 1px;
                text-align: left;
                margin-bottom: 10px;
            }
            
            .description {
                font-size: 14px;
                width: 72%;
                float: right;
                text-align: right;
            }

            .bottom {
                clear: both;
                justify-content: flex-end;
                position: absolute;
                bottom: 15px;

                .btn-order {
                    display: none;
                    position: absolute;
                    font-size: 14px;
                    right: 0;
                    border-radius: 10px 0px 10px 0px;
                    transition: border 0.3s;
                    min-width: 100px;
                    margin-right: -5px ;

                    &:hover { border: 1px solid white }
                }
            }
        }
    }

    @media (min-width: 760px) {
        width: 340px;
        margin: auto auto 45px auto;
    }

    @media (min-width: 1180px) {
        margin: auto auto 50px auto;
    }
}