@import 'assets/css/_mixins.scss';


.lc-header-black { 
    box-shadow: inset 0px 31px 72px -12px rgba(130,130,130,0.8);

    @media (min-width: 1024px){
        box-shadow: inset 0px 31px 72px -12px rgba(230,230,230,0.8);
    }
}
.hidden-header {
    @include transform(translateY(-95px))
}
.no-hidden { transform: none }

#lc_header {
    position: fixed;
    width: 100%;
    z-index: 10;
    @include transition(all 0.5s);

    .lc-container-header {
        font-family: $SeogeScript-Regular;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 8px 10px 8px 10px; 

        // LEFT
        .left {
            position: relative;
            z-index: 20;
        
            .logo { width: 80px }

            @media (min-width: 1024px) {
                .logo { width: 100px }
            }
        }

        // CENTER
        .center {     
            .show-menu { 
                visibility: visible !important;
                opacity: 1 !important
            }
            .container-items {
                opacity: 0;
                visibility: hidden;
                z-index: 10;
                position: fixed;
                width: 100%;
                height: 100%;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background-color: $main-color-1;
                overflow: hidden;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: opacity 0.3s, visibility 0.3s ease-in;

                .items { 
                    list-style: none;
                    text-transform: uppercase;
                    max-height: 80vh;
                    overflow-y: auto;
                    
                    .item-active { opacity: 0.8 }
                    li {
                        font-size: 28px;
                        text-align: center;
                        padding: 5px;
                        margin-bottom: 25px;
                        a { color: #FFFFFF; text-decoration: none }
                        &:last-child { margin-bottom: 0px }
                    }
                }
            }

            @media (min-width: 1024px) {
                .container-items  {
                    position: relative;
                    visibility: visible !important; 
                    opacity: 1 !important;
                    background-color: transparent;

                    .items {
                        display: flex;
                        align-items: center;
                        text-transform: inherit;
                        
                        .item-active { opacity: 1; color: $main-color-1 }
                        li {
                            font-size: 17px;
                            margin-bottom: 0px;
                            margin-right: 10px;
                            
                            &:last-child { margin-right: 0px }
                            a { 
                                color: #000000;
                                @include transition(color 0.3s);
                                &:hover { color: $main-color-1 }
                            }
                        }
                    }
                }
                .btn-order-now { display: none }
            }
        }


        // RIGHT
        .right {
            position: relative;
            z-index: 20;

            .btn-order-now { 
                font-family: $Overpass-Regular;
                font-size: 14px;
                display: none; 
                border: 1px solid transparent;
                background-color: $main-color-2;
                text-decoration: none;
                transition: background-color 0.3s, color 0.3s ease-in;

                &:hover {
                    background-color: #FFFFFF;
                    color: $main-color-2;
                    border: 1px solid $main-color-2 !important;
                }
            }
            #icon_menu {
                font-size: 30px;
                color: #FFFFFF;
                cursor: pointer
            }

            @media (min-width: 1024px) {
                .btn-order-now { display: block }
                #icon_menu { display: none }
            }
            @media (min-width: 1180px) {
                .btn-order-now { margin-right: 35px }
            }
        }
    }
}