@import 'assets/css/_mixins.scss';

.main-slider {
    padding-left: 0px !important;
    padding-right: 0px !important;
    overflow: hidden;

    .content-slider {
        position: relative;
        display: flex;
        flex-direction: column-reverse;

        // LEFT
        .left {
            padding: 25px 10px 25px 10px;
            
            // Data
            .data {
                .text {
                    margin-bottom: 25px;

                    .small-title {
                        font-size: 22px;
                        margin-bottom: -5px
                    }
                    .big-title {
                        font-size: 33px;
                        margin-bottom: 8px;
                    }
                }
                .buttons {
                    margin-top: 45px; 
                    margin-bottom: 8px;                     
                }
            }
            // Social Networks
            .social-networks { display: none }

            @media (min-width: 760px) {
                min-width: 277px;

                // Data
                .data {
                    .text {
                        margin-bottom: 30px;

                        .small-title { font-size: 27px; margin-bottom: -8px }
                        .big-title { font-size: 38px }
                    }
                } 
            }

            @media (min-width: 1024px) {
                .social-networks {
                    display: flex;
                    align-items: center;
                    position: absolute;
                    bottom: 0px;

                    .social-icon {
                        width: 25px;
                        margin: 3px 8px -5px 3px;
                        @include transition(opacity 0.3s);

                        &:hover { opacity: 0.7 }
                    }
                }
            }

            @media (min-width: 1180px) {
                // Data
                .data {
                    .text {
                        margin-bottom: 45px;
                        .small-title { font-size: 35px }
                        .big-title { font-size: 50px }
                    }
                }
            }

            @media (min-width: 1280px) {
                // Data
                .data {
                    .text {
                        .big-title { min-width: 440px }
                    }
                } 
            }

            @media (min-width: 1300px) {
                // Data
                .data {
                    .text {
                        .small-title { font-size: 40px }
                        .big-title { font-size: 60px }
                    }
                } 
            }

            @media (min-width: 1180px) {
                padding-bottom: 0px
            }
         }

        // Right
        .right {
            position: relative;

            .slider {
                .container-img-slide {
                    width: 70%;
                    z-index: 3;
                    position: relative;
                    overflow: hidden;
                    margin: 2% 5% 5% 5%;

                    .img-slide {
                        width: 100%;
                    }
                }

                .controls {
                    position: absolute;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    width: 60%;
                    background-color: $main-color-1;
                    border-radius: 0px 0px 0px 25px;

                    &:before {
                        content: '';
                        position: absolute;
                        background-color: $main-color-1;
                        width: 100%;
                        height: 100%;
                        top: -100%;
                    }
                    .lc-placeholder {
                        position: absolute;
                        width: 90%;
                        height: 88%;
                        bottom: 0;
                        right: 0;
                        margin-bottom: 30px;
                        @include transform(rotate(-90deg) translate(0px, -40px));
                        
                        &:before {
                            content: 'Lechoncitos';
                            font-family: $SeogeScript-Regular;
                            font-weight: 900;
                            font-size: 40px;
                            opacity: 0.1;
                            position: absolute;
                            bottom: 0;
                            line-height: 38px                            
                        }

                        @media (min-width: 500px) and (orientation: landscape) {
                            @include transform(rotate(-90deg) translate(0px, -80px));
                            &:before { font-size: 70px }
                        }

                        @media (min-width: 760px) {
                            @include transform(rotate(-90deg) translate(0px, -50px));
                            width: 100%;
                            max-width: 400px;

                            &:before { font-size: 52px }
                        }

                        @media (min-width: 1024px) {
                            @include transform(rotate(-90deg) translate(0px, -70px));
                            &:before { font-size: 70px }
                        }
                    }
                    .arrows {
                        position: absolute;
                        bottom: 0;
                        right: 0;
                        margin-top: 15px;
                        display: flex;
                        align-items: flex-end;

                        .arrow {
                            color: #FFFFFF;
                            background-color: #e8680b;
                            cursor: pointer;
                            width: 40px;
                            height: 32px;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            span { margin-left: 5px }
                        }
                        .arrow-active {
                            background-color: $main-color-2;
                            border-top: 2px solid $main-color-2
                        }

                        @media (min-width: 1180px) {
                            .arrow {
                                width: 50px;
                                height: 40px;
                                @include transition(opacity 0.3s);

                                &:hover { opacity: 0.8 }
                            }
                        }
                    }
                }
            }

            @media (min-width: 760px) {
                .slider {
                    .container-img-slide {
                        margin: 0%;
                        width: 80%;
                    }

                    .slider-indicators {
                        display: flex;
                        align-items: center;
                        left: 0;
                        
                        .indicator {
                            width: 13px;
                            height: 6px;
                            background-color: $main-color-1;
                            margin-right: 5px;
                            opacity: 0.7;
                            cursor: pointer;
                            @include transition(width 0.3s);

                            &:hover {
                                width: 18px;
                                opacity: 1;
                            }
                            &:last-child { margin-right: 0px }
                        }
                        .indicator-active {
                            width: 18px;
                            background-color: $main-color-2;
                            opacity: 1;
                        }
                    }
                }
            }
            @media (min-width: 1024px) {
                .slider {
                    width: 650px;
                
                    .container-img-slide {
                        width: 75%;
                        max-height: 490px;
                    }
                }
            }
        }

        @media (min-width: 760px) {
            flex-direction: row;
            align-items: center;
        }
    }

    @media (min-width: 760px) {
        padding-left: 45px !important;
    }

    @media (min-width: 1180px) {
        padding-left: 0px !important;
    }

    // Slider Animations
    // Image
    .image-enter {
        @include transform(translateY(-120px));
        opacity: 0;
    }
    .image-enter-active {
        @include transform(translateY(0px));
        @include transition(all 0.7s);
        opacity: 1;
    }

    .image-exit {
        @include transform(translateY(0px));
        @include transition(all 0.7s);
        position: absolute !important;
        opacity: 0;
    }
    .image-exit-active {
        @include transform(translateY(-120px));
        opacity: 0;
    }
    // Text
    .text-enter {
        @include transform(translateX(-120px));
        opacity: 0;
    }
    .text-enter-active {
        @include transform(translateX(0px));
        @include transition(all 0.9s);
        opacity: 1;
    }

    .text-exit {
        @include transform(translateX(0px));
        @include transition(all 0.9s);
        position: absolute !important;
        opacity: 0;
    }
    .text-exit-active {
        @include transform(translateX(-120px));
        opacity: 0;
    }
    // Buttons
    .buttons-enter {
        @include transform(translateX(120px));
        opacity: 0;
    }
    .buttons-enter-active {
        @include transform(translateX(0px));
        @include transition(all 0.9s);
        opacity: 1;
    }
    
    .buttons-exit {
        @include transform(translateX(0px));
        @include transition(all 0.9s);
        position: absolute !important;
        opacity: 0;
    }
    .buttons-exit-active {
        @include transform(translateX(120px));
        opacity: 0;
    }
}